import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import { connect } from 'react-redux'
import Layout from 'antd/es/layout'
import Tooltip from 'antd/es/tooltip'
import Menu from 'antd/es/menu'
import Avatar from 'antd/es/avatar'
import Drawer from 'antd/es/drawer'
import Button from 'antd/es/button'
import * as classes from './Layout.module.css'
import AimOutlined from '@ant-design/icons/AimOutlined'
import LogoutOutlined from '@ant-design/icons/LogoutOutlined'
import UserOutlined from '@ant-design/icons/UserOutlined'
import HeartFilled from '@ant-design/icons/HeartFilled'
import RocketFilled from '@ant-design/icons/RocketFilled'
import TeamOutlined from '@ant-design/icons/TeamOutlined'
import MenuOutlined from '@ant-design/icons/MenuOutlined'
import HomeFilled from '@ant-design/icons/HomeFilled'
import ShopOutlined from '@ant-design/icons/ShopOutlined'
import PageHeader from 'antd/es/page-header'
import getAvatar from '../../utils/getAvatar'
import Account from '../../containers/Account/Account'

const { Header, Content } = Layout

const AccountLayout = props => {
    const { account } = props
    const [menuVisible, setMenuVisible] = useState()

    const toggleMenu = () => {
        setMenuVisible(!menuVisible)
    }
    const selectedKey =
        props.history.location.pathname.split('/')[1] || undefined

    const buttonProps = {
        type: 'link',
        // ghost: true,
        size: 'large',

        className: classes.Button,
    }
    const checkHighligh = url =>
        selectedKey === url ? { ghost: false, type: 'primary' } : {}
    const openLink = url => props.history.push('/' + url)

    const isActivating = props.history.location.pathname === '/activate-account'

    return props.isAuthed ? (
        <>
            <Layout className={classes.Layout}>
                <Header className={classes.Header}>
                    <div className={classes.TopHeader}>
                        <div className={classes.MenuButtonContiner}>
                            {!isActivating && (
                                <Tooltip
                                    title="Menu"
                                    placement="bottomLeft"
                                    mouseEnterDelay={0.3}
                                >
                                    <Button
                                        {...buttonProps}
                                        icon={<MenuOutlined />}
                                        onClick={toggleMenu}
                                    />
                                </Tooltip>
                            )}
                        </div>
                        <div className={classes.QuickButtonContiner}>
                            {/* <Tooltip
                                title="Dashboard"
                                placement="bottomRight"
                                mouseEnterDelay={0.3}
                            >
                                <Button
                                    {...buttonProps}
                                    {...checkHighligh('dashboard')}
                                    icon={<HomeFilled />}
                                    onClick={() => openLink('dashboard')}
                                />
                            </Tooltip> */}
                            {!isActivating ? (
                                <>
                                    <Tooltip
                                        title="Progresses"
                                        placement="bottomRight"
                                        mouseEnterDelay={0.3}
                                    >
                                        <Button
                                            {...buttonProps}
                                            {...checkHighligh('progresses')}
                                            icon={<RocketFilled />}
                                            onClick={() =>
                                                openLink('progresses')
                                            }
                                        />
                                    </Tooltip>
                                    <Tooltip
                                        title="Shop"
                                        placement="bottomRight"
                                        mouseEnterDelay={0.3}
                                    >
                                        <Button
                                            {...buttonProps}
                                            {...checkHighligh('shop')}
                                            icon={<ShopOutlined />}
                                            onClick={() => openLink('shop')}
                                        />
                                    </Tooltip>
                                    <Tooltip
                                        title="Friends"
                                        placement="bottomRight"
                                        mouseEnterDelay={0.3}
                                    >
                                        <Button
                                            {...buttonProps}
                                            {...checkHighligh('friends')}
                                            icon={<TeamOutlined />}
                                            onClick={() => openLink('friends')}
                                        />
                                    </Tooltip>
                                    <Tooltip
                                        title="Account"
                                        placement="bottomRight"
                                        mouseEnterDelay={0.3}
                                    >
                                        <span
                                            onClick={() => openLink('account')}
                                            style={{
                                                margin: '0px 10px',
                                                cursor: 'pointer',
                                            }}
                                        >
                                            <Avatar
                                                src={getAvatar(
                                                    account._id,
                                                    account.name
                                                )}
                                            />
                                        </span>
                                    </Tooltip>
                                </>
                            ) : (
                                <Tooltip
                                    title="Logout"
                                    placement="bottomRight"
                                    mouseEnterDelay={0.3}
                                >
                                    <Button
                                        {...buttonProps}
                                        {...checkHighligh('friends')}
                                        icon={<LogoutOutlined />}
                                        onClick={() =>
                                            props.history.push('/logout')
                                        }
                                    >
                                        Logout
                                    </Button>
                                </Tooltip>
                            )}
                        </div>
                    </div>
                    <div className={classes.BottomHeader}>
                        <PageHeader
                            className="site-page-header"
                            onBack={() => props.history.goBack()}
                            title={
                                isActivating
                                    ? 'Activate your account'
                                    : props.pageTitle && props.pageTitle.main
                            }
                            subTitle={
                                (props.pageTitle && props.pageTitle.sub) || ''
                            }
                        />
                    </div>
                </Header>
                <Content className={classes.Main}>
                    <div>{props.children}</div>
                </Content>
                {!isActivating && (
                    <Drawer
                        title="Menu"
                        closable={true}
                        onClose={toggleMenu}
                        visible={menuVisible}
                        placement="left"
                    >
                        <Menu
                            theme="light"
                            mode="vertical"
                            selectedKeys={[selectedKey]}
                            onClick={({ key }) => {
                                props.history.push('/' + key)
                                toggleMenu()
                            }}
                            overflowedIndicator={<MenuOutlined />}
                        >
                            {/* <Menu.Item key="dashboard" icon={<HomeFilled />}>
                            Dashboard
                        </Menu.Item> */}
                            <Menu.Item key="progresses" icon={<RocketFilled />}>
                                Progresses
                            </Menu.Item>
                            <Menu.Item key="goals" icon={<AimOutlined />}>
                                Goals
                            </Menu.Item>
                            <Menu.Item key="shop" icon={<ShopOutlined />}>
                                Shop
                            </Menu.Item>
                            <Menu.Item key="friends" icon={<TeamOutlined />}>
                                Friends
                            </Menu.Item>
                            <Menu.Item key="wishlist" icon={<HeartFilled />}>
                                Wishlist
                            </Menu.Item>
                            <Menu.Item key="account" icon={<UserOutlined />}>
                                Account
                            </Menu.Item>
                            <Menu.Item key="logout" icon={<LogoutOutlined />}>
                                Logout
                            </Menu.Item>
                        </Menu>
                    </Drawer>
                )}
            </Layout>
        </>
    ) : ['/login', 'logout'].includes(props.history.location.pathname) ? (
        props.children
    ) : (
        <>
            <Layout className={classes.Layout}>
                <Header className={classes.Header}>
                    <div className={classes.TopHeader}>
                        <div className={classes.MenuButtonContiner} />
                        <div className={classes.QuickButtonContiner}>
                            <Tooltip
                                title="Sign in or Sign up"
                                placement="bottomRight"
                                mouseEnterDelay={0.3}
                            >
                                <Button
                                    {...buttonProps}
                                    {...checkHighligh('friends')}
                                    icon={<LogoutOutlined />}
                                    onClick={() => props.history.push('/login')}
                                >
                                    Sign in or Sign up
                                </Button>
                            </Tooltip>
                        </div>
                    </div>
                    <div className={classes.BottomHeader}>
                        <PageHeader
                            className="site-page-header"
                            onBack={() => props.history.goBack()}
                            title={props.title && props.title.main}
                            subTitle={(props.title && props.title.sub) || ''}
                        />
                    </div>
                </Header>
                <Content className={classes.Main}>
                    <div>{props.children}</div>
                </Content>
            </Layout>
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        pageTitle: state.pageTitle,
        isAuthed: state.isAuthed,
    }
}

export default withRouter(connect(mapStateToProps)(AccountLayout))
