import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'
import Switch from 'antd/es/switch'
import Tabs from 'antd/es/tabs'
import Divider from 'antd/es/divider'
import Upload from 'antd/es/upload'
import List from 'antd/es/list'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import MinusCircleOutlined from '@ant-design/icons/MinusCircleOutlined'

import Avatar from 'antd/es/avatar'
import { uploadFile } from '../../utils/uploadFile'
import Rewards from '../Rewards/Rewards'
import DraggableTabs from '../UI/DraggableTabs/DraggableTabs'

const EditGoalAndProgressForm = props => {
    const { goal, deleteGoal } = props

    const [milestones, setMilestones] = useState(
        (goal && goal.milestones) || [{ name: 'Milestone 1', key: 'mile1' }]
    )

    const [activeKey, setActiveKey] = useState('mile1')
    const [images, setImages] = useState(
        (goal &&
            goal.images.map((url, index) => ({
                url,
                uid: index,
            }))) ||
            []
    )
    const [withMilestones, setWithMilestones] = useState(
        goal && goal.withMilestones
    )
    const [previewImage, setPreviewImage] = useState('')
    const [newTabIndex, setNewTabIndex] = useState(
        (goal && goal.newTabIndex) || 2
    )

    const goalId = props.goalId || ''

    const [rewards, setRewards] = useState((goal && goal.rewardsGroups) || [])

    const onFinish = value => {
        const milestonesValues = []
        for (let milestone of milestones) {
            milestonesValues.push({
                ...value[milestone.key],
                key: milestone.key,
                name: milestone.name,
            })
        }

        props.onFinish({
            ...value,
            images: images.map(image => image.url),
            milestones,
            milestonesValues,
            newTabIndex,
            rewardsGroups: rewards,
        })
    }

    const onChange = aK => {
        setActiveKey(aK)
    }

    const onEdit = (targetKey, action) => {
        if (action === 'add') add(targetKey)
        else remove(targetKey)
    }

    const add = () => {
        const newPanes = [...milestones]
        const newKey = `mile${newTabIndex}`
        newPanes.push({ key: newKey, name: 'New milestone' })
        setMilestones(newPanes)
        setActiveKey(newKey)
        setNewTabIndex(newTabIndex + 1)
    }

    const remove = targetKey => {
        let newActiveKey = activeKey
        let lastIndex
        milestones.forEach((pane, i) => {
            if (pane.key === targetKey) {
                lastIndex = i - 1
            }
        })
        const newPanes = milestones.filter(pane => pane.key !== targetKey)
        if (newPanes.length && newActiveKey === targetKey) {
            if (lastIndex >= 0) {
                newActiveKey = newPanes[lastIndex].key
            } else {
                newActiveKey = newPanes[0].key
            }
        }
        setMilestones(newPanes)
        setActiveKey(newActiveKey)
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    const connections = props.account.friendsData

    const connectionsSelect = (
        <Select
            mode="multiple"
            style={{ width: '100%' }}
            placeholder="Please select"
        >
            {connections.map(item => (
                <Select.Option key={item._id}>
                    <Avatar
                        src={
                            'https://websiter.s3.us-east-2.amazonaws.com/' +
                            item._id +
                            '/avatar'
                        }
                        size={20}
                        style={{ marginRight: '10px' }}
                    />
                    {item.name} - ({item._id})
                </Select.Option>
            ))}
        </Select>
    )
    const [form] = Form.useForm()

    const normFile = e => {
        if (Array.isArray(e)) {
            return e
        }
        return e && e.fileList.map(image => image.url)
    }

    return !props.goalId || goal ? (
        <>
            <Form
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    goal || {
                        name: 'New goal',
                        mile1: { name: 'Milestone 1', key: 'mile1' },
                    }
                }
                {...layout}
                onValuesChange={changedValues => {
                    for (let attr in changedValues) {
                        const index = milestones.findIndex(
                            item => item.key === attr
                        )

                        if (index > -1) {
                            if (changedValues[attr].name) {
                                const newMilestones = [...milestones]
                                newMilestones[index] = {
                                    ...newMilestones[index],
                                    name: changedValues[attr].name,
                                }
                                setMilestones(newMilestones)
                            }
                        }
                        if (attr === 'withMilestones') {
                            setWithMilestones(changedValues[attr])
                        }
                    }
                }}
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    getValueFromEvent={normFile}
                >
                    <Upload
                        listType="picture-card"
                        fileList={images.map((image, index) => ({
                            ...image,
                            uid: index,
                        }))}
                        onPreview={file => {
                            setPreviewImage(file.url)
                        }}
                        onChange={({ fileList }) => setImages(fileList)}
                        customRequest={({ file }) => {
                            uploadFile(file, 800, undefined, url => {
                                setImages(
                                    [...images, { url }].map(
                                        (image, index) => ({
                                            ...image,
                                            uid: index,
                                        })
                                    )
                                )
                            })
                        }}
                    >
                        {images.length >= 8 ? null : (
                            <div>
                                <PlusOutlined />
                                <div className="ant-upload-text">Upload</div>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Form.Item
                    label="Name"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: 'Please input the goal name.',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item label="Description" name="description">
                    <Input.TextArea placeholder="Write a few words about why this goal is important and how to achieve it." />
                </Form.Item>

                {props.isGoal && (
                    <Form.Item label="Available for" name="users">
                        {connectionsSelect}
                    </Form.Item>
                )}
                <Form.Item label="Experts" name="experts">
                    {connectionsSelect}
                </Form.Item>
                <Form.Item label="Supporters" name="supporters">
                    {connectionsSelect}
                </Form.Item>

                <Rewards
                    rewardKey="start"
                    label="Start rewards"
                    rewards={rewards}
                    setRewards={setRewards}
                />
                <Rewards
                    rewardKey="finish"
                    label="Finish rewards"
                    rewards={rewards}
                    setRewards={setRewards}
                />
                <Form.Item
                    label="Milestones"
                    name="withMilestones"
                    valuePropName="checked"
                >
                    <Switch />
                </Form.Item>

                {withMilestones && (
                    <>
                        <Divider>Milestones</Divider>

                        <DraggableTabs
                            tabPosition="left"
                            type="editable-card"
                            onChange={onChange}
                            activeKey={activeKey || 'mile1'}
                            onEdit={onEdit}
                        >
                            {milestones.map(milestone => (
                                <Tabs.TabPane
                                    tab={milestone.name}
                                    key={milestone.key}
                                >
                                    <Form.Item
                                        label="Name"
                                        name={[milestone.key, 'name']}
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Missing name',
                                            },
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                    <Form.Item
                                        label="Description"
                                        name={[milestone.key, 'description']}
                                    >
                                        <Input.TextArea placeholder="Write a few words about why this milestone is important and how to achieve it." />
                                    </Form.Item>
                                    <Rewards
                                        rewardKey={milestone.key}
                                        label="Rewards"
                                        rewards={rewards}
                                        setRewards={setRewards}
                                    />
                                </Tabs.TabPane>
                            ))}
                        </DraggableTabs>
                    </>
                )}
                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>

                    {goalId && props.isGoal && (
                        <Button
                            type="link"
                            onClick={deleteGoal}
                            danger
                            size="large"
                        >
                            Delete
                        </Button>
                    )}
                </Form.Item>
            </Form>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default EditGoalAndProgressForm
