import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Descriptions from 'antd/es/descriptions'
import Modal from 'antd/es/modal'
import Avatar from 'antd/es/avatar'
import Button from 'antd/es/button'
import getAvatar from '../../utils/getAvatar'

const { Item } = Descriptions

const Perk = props => {
    const [previewImage, setPreviewImage] = useState('')
    useEffect(() => {
        pullData(props, {})
    }, [])

    const { perk, profile } = props
    if (perk) {
        props.setPageTitle({ main: perk.name, sub: profile._id })
    }

    const getAccounts = items => {
        if (profile && profile.friendsData) {
            return items.map(item => {
                const friendObj = profile.friendsData.find(
                    friend => friend._id === item
                )
                if (friendObj) {
                    return (
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push('/profile/' + item)
                            }
                        >
                            <Avatar
                                src={getAvatar(item, friendObj.image)}
                                size={20}
                                style={{ marginRight: '10px' }}
                            />
                            {friendObj.name} - ({item})
                        </span>
                    )
                } else return item
            })
        } else return items
    }

    return perk ? (
        <Card
            title={
                <span>
                    Item from{' '}
                    <Button
                        type="link"
                        style={{ padding: 0 }}
                        onClick={() =>
                            props.history.push('/profile/' + profile._id)
                        }
                    >
                        <Avatar
                            src={getAvatar(profile._id, profile.image)}
                            style={{ marginRight: '5px' }}
                        />
                        {profile.name + ' (' + profile._id + ')'}
                    </Button>{' '}
                    shop
                </span>
            }
        >
            {perk.images.map(image => (
                <span onClick={() => setPreviewImage(image)}>
                    <Card
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        hoverable
                        cover={
                            <div
                                style={{
                                    backgroundImage: 'url(' + image + ')',
                                    height: 200,
                                    width: 200,
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                    />
                </span>
            ))}
            <p>{perk.description}</p>

            <Descriptions
                title="Goal description"
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Available for">{getAccounts(perk.users)}</Item>
                <Item label="Price">{perk.price}</Item>
            </Descriptions>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </Card>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        perk: state.perk,
        profile: state.profile,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Perk)
)
