import message from 'antd/es/message'

export default (res, props) => {
    if (res.data.logout) props.history.push('/logout')
    if (res.data.success === false) {
        if (res.data.errorCode) {
            if (res.data.errorCode === 'no account')
                message.error('Account is not found. Please login.')
            if (res.data.errorCode === 'bad data')
                message.error(
                    'Error. Please try one more time with correct data.'
                )
        } else {
            message.error('Something failed')
        }
    } else {
        if (res.data.successCode) {
            switch (res.data.successCode) {
                case 'avatar saved':
                    message.success('The avatar image has been updated.')
                    break
                case 'success':
                    message.success('Success!')
                    break
                case 'item saved':
                    message.success('Item is saved')
                    break
                default:
                    message.success('Success!')
                    break
            }
        }
    }
    if (res.data.status && res.data.status === 'no account')
        props.history.push('/activate-account')
}
