import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'

const WishlistItem = props => {
    const [previewImage, setPreviewImage] = useState('')
    useEffect(() => {
        pullData(props, {})
    }, [])

    const { wishlistItem, profile } = props
    if (wishlistItem) {
        props.setPageTitle({ main: wishlistItem.name, sub: profile._id })
    }
    return wishlistItem ? (
        <Card
            title={
                <span>
                    Item from{' '}
                    <Button
                        type="link"
                        style={{ padding: 0 }}
                        onClick={() =>
                            props.history.push('/profile/' + profile._id)
                        }
                    >
                        <Avatar
                            src={getAvatar(profile._id, profile.image)}
                            style={{ marginRight: '5px' }}
                        />
                        {profile.name + ' (' + profile._id + ')'}
                    </Button>{' '}
                    wishlist
                </span>
            }
        >
            {wishlistItem.images.map(image => (
                <span onClick={() => setPreviewImage(image)}>
                    <Card
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        hoverable
                        cover={
                            <div
                                style={{
                                    backgroundImage: 'url(' + image + ')',
                                    height: 200,
                                    width: 200,
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                    />
                </span>
            ))}
            <h1>{wishlistItem.name}</h1>
            <p>{wishlistItem.description}</p>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </Card>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        wishlistItem: state.wishlistItem,
        profile: state.profile,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(WishlistItem)
)
