import React from 'react'
import axios from 'axios'
import { withRouter } from 'react-router-dom'

import { Component } from 'react'
import * as wsActions from '../../websocketActions'
const diffpatcher = require('jsondiffpatch/dist/jsondiffpatch.umd.js').create({
    propertyFilter: (name, context) => name !== 'patch',
})

export let webSocket = false

let currentCallbackId = 0
const callbacks = {}

export const addWSCallback = callback => {
    currentCallbackId++
    const callbackId = 'cb_' + currentCallbackId
    callbacks[callbackId] = callback
    return callbackId
}

// const fireCallback = id => {
//     if (callbacks[id]) {
//         callbacks[id]()
//         delete callbacks[id]
//     }
// }

class ReserveWebsite extends Component {
    start = () => {
        let url
        if (process.env.NODE_ENV === 'development') {
            url = 'ws://my.websiter.test:5000'
        } else {
            url = 'wss://getreward.herokuapp.com'
        }
        let pingTimeout
        webSocket = new WebSocket(url)

        const heartbeat = () => {
            clearTimeout(pingTimeout)
            pingTimeout = setTimeout(() => {
                webSocket.close()
            }, 30000 + 1000)

            webSocket.send(
                JSON.stringify({
                    messageCode: 'heartbeat',
                })
            )
        }

        webSocket.onopen = event => {
            webSocket.send(
                JSON.stringify({
                    messageCode: 'auth',
                    user: axios.defaults.headers.common['x-auth-token'],
                    progressId: this.props.data.progress._id,
                })
            )
            heartbeat()
        }
        webSocket.onmessage = event => {
            const news = JSON.parse(event.data)

            switch (news.messageCode) {
                case 'heartbeat':
                    heartbeat()
                    break
                case 'logout':
                    this.props.logout()
                    break
                case 'messageSaved':
                    this.props.setState({
                        ...this.props.state,
                        messageValue: '',
                    })
                    break
                case 'friendsData':
                    this.props.setData({
                        ...this.props.data,
                        account: {
                            ...this.props.data.account,
                            friendsData: news.friendsData,
                        },
                    })
                    break
                case 'redirectToProgresses':
                    this.props.history.push('/progresses')
                    break
                case 'update':
                    let newProgress
                    if (
                        this.props.data.progress.__v ===
                        news.progress.__v - 1
                    ) {
                        newProgress = diffpatcher.patch(
                            this.props.data.progress,
                            news.progress.patch
                        )
                        newProgress.__v = news.progress.__v
                        this.props.setData({
                            ...this.props.data,
                            progress: newProgress,
                        })
                    }
                    wsActions.getFriendsData(
                        newProgress || this.props.data.progress,
                        this.props.data.account.friends
                    )
                    break
                case 'error':
                    alert(news.text)
                    break
                default:
                    break
            }
        }
        webSocket.onclose = event => {
            clearTimeout(pingTimeout)
        }
    }

    checkInterval = null

    check = () => {
        if (!webSocket || webSocket.readyState === 3) {
            this.start()
        }
    }
    componentWillMount() {
        this.checkInterval = setInterval(this.check, 5000)
    }

    componentWillUnmount() {
        clearInterval(this.checkInterval)
        if (webSocket) {
            webSocket.close()
        }
    }

    render() {
        return <span />
    }
}

export default withRouter(ReserveWebsite)
