import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Button from 'antd/es/button'
import PageHeader from 'antd/es/page-header'
import Descriptions from 'antd/es/descriptions'
import Tabs from 'antd/es/tabs'
import Divider from 'antd/es/divider'
import Modal from 'antd/es/modal'
import Drawer from 'antd/es/drawer'
import Avatar from 'antd/es/avatar'
import Messages from './Messages'
import Activity from './Activity'
import ProgressWebsocket from '../ProgressWebsocket/ProgressWebsocket'

const { Meta } = Card
const { Item } = Descriptions

const Progress = props => {
    const [data, setData] = useState({})
    const [state, setState] = useState({})

    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])

    const { progress, account, profile } = data

    if (!progress) return null

    const { goal } = progress
    if (goal && props.setTitle) {
        props.setTitle({ main: goal.name, sub: progress.worker })
    }

    return goal ? (
        <>
            {account && (
                <ProgressWebsocket
                    data={data}
                    setData={setData}
                    state={state}
                    setState={setState}
                />
            )}

            <Messages
                data={data}
                setData={setData}
                state={state}
                setState={setState}
            />

            <Drawer
                title="Progress activity and settings"
                placement="right"
                closable={true}
                onClose={() => setState({ ...state, activityOpen: false })}
                visible={state.activityOpen}
                width={'100%'}
            >
                <Activity
                    data={data}
                    setData={setData}
                    state={state}
                    setState={setState}
                />
            </Drawer>
            <Modal
                visible={state.previewImage}
                footer={null}
                onCancel={() => setState({ ...state, previewImage: '' })}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={state.previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default withRouter(Progress)
