import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Avatar from 'antd/es/avatar'
import Card from 'antd/es/card'

import friendActionButton from '../../components/FriendActionButton'
import FriendCard from '../../components/FriendCard'
import pullData from '../../utils/pullData'
import Tabs from 'antd/es/tabs'
import Progresses from '../Progresses/Progresses'
import GoalCard from '../Goals/GoalCard'
import Wallet from '../Shop/Wallet'
import PerkCard from '../Shop/PerkCard'
import getAvatar from '../../utils/getAvatar'
import { acceptFriend, unfriend, addFriend } from '../Friends/methods'

const Profile = props => {
    const [data, setData] = useState()

    useEffect(() => {
        pullData(props, {
            forceAuth: false,
            forceActivate: false,
        })
    }, [props.history.location.pathname])

    const { profile, account } = props
    if (!profile._id) return null
    props.setPageTitle({ main: profile.name, sub: profile._id })

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (account && account.friends) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }
    const friendActions = friendActionButton({
        accountId: account ? account._id : '',
        realFriends: realFriends,
        invitedFriends: invitedFriends,
        invitingFriends: invitingFriends,
        _id: profile._id,
        acceptFriend: acceptFriend,
        unfriend: unfriend,
        addFriend: addFriend,
        additionalButton: null,
    })
    return (
        <Card>
            <div style={{ textAlign: 'center', marginBottom: '50px' }}>
                <Avatar
                    src={getAvatar(profile._is, profile.image)}
                    size={200}
                />
                <h1>{profile.name}</h1>
                <h3>({profile._id})</h3>
                {friendActions}
            </div>
            <Tabs>
                <Tabs.TabPane tab="Friends" key={1}>
                    <Card title="Friends">
                        {profile.friends
                            .filter(friend => friend.status === 'friend')
                            .map(friend => {
                                const friendData = {
                                    ...profile.friendsData.find(
                                        item =>
                                            item._id.toString() ===
                                            friend.friend.toString()
                                    ),
                                    ...friend.friend,
                                }

                                return (
                                    <FriendCard
                                        key={friendData._id}
                                        {...props}
                                        account={account}
                                        friend={friendData}
                                        realFriends={realFriends}
                                        invitingFriends={invitingFriends}
                                        invitedFriends={invitedFriends}
                                        data={data}
                                        setData={setData}
                                    />
                                )
                            })}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Progresses" key={2}>
                    <Card title="Progresses">
                        <Progresses
                            account={profile}
                            progressesData={profile.progressesData}
                            keepPageTitle={true}
                        />
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Goals" key={3}>
                    <Card title="Goals">
                        {profile.goals.map((goal, index) => (
                            <GoalCard
                                key={index}
                                goal={goal}
                                owner={profile}
                                available={goal.users.includes(account._id)}
                                account={account}
                            />
                        ))}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Shop" key={4}>
                    <Card title="Shop">
                        {profile.perks &&
                            profile.perks.map((perk, index) => (
                                <PerkCard
                                    key={index}
                                    perk={perk}
                                    owner={profile}
                                    account={account}
                                    onlyBuy={true}
                                    data={data}
                                    setData={setData}
                                    mode="shop"
                                />
                            ))}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Wallet" key={5}>
                    <Card title="Wallet">
                        <Wallet data={{ account: profile }} />
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Wishlist" key={6}>
                    <Card title="Wishlist">
                        {profile.wishlist.map((item, index) => (
                            <PerkCard
                                key={index}
                                perk={item}
                                owner={profile}
                                account={account}
                                setEditedPerk={() => {}}
                                setEditPerkOpen={() => {}}
                            />
                        ))}
                    </Card>
                </Tabs.TabPane>
            </Tabs>
        </Card>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Profile)
)
