import { webSocket } from '../containers/ProgressWebsocket/ProgressWebsocket'

export const sendMessage = (state, data, imageUrl) => {
    if (webSocket && webSocket.readyState !== 3) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'sendMessage',
                messageValue: state.messageValue,
                editedMessage: state.editedMessage,
                replyToMessage: state.replyToMessage,
                imageUrl,
                progressId: data.progress._id,
                accountId: data.account._id,
            })
        )
    }
}

