import axios from 'axios'
import processRes from './processRes'
export default (props, options, callback) => {
    if (typeof props.isAuthed === 'undefined') return
    options = {
        forceAuth: true,
        forceActivate: true,
        pullData: true,
        ...(options || {}),
    }

    if (!props.isAuthed) {
        props.tryAuth(props, options.forceAuth)
    }

    if (options.pullData) {
        axios
            .get('/api' + props.history.location.pathname)
            .then(res => {
                processRes(res, props)
                if (options.forceActivate) {
                    if (props.isAuthed && res.data.status === 'no account') {
                        props.history.push('/activate-account')
                    }
                }
                props.setData(res.data)
            })
            .catch(err => {})
    }
}
