import React from 'react'
import { connect } from 'react-redux'
import * as actions from '../store/actions'
import { withRouter } from 'react-router-dom'

import Card from 'antd/es/card'
import Avatar from 'antd/es/avatar'

import Tooltip from 'antd/es/tooltip'
import EyeOutlined from '@ant-design/icons/EyeOutlined'
import friendActionButton from './FriendActionButton'
import getAvatar from '../utils/getAvatar'
import {
    acceptFriend,
    unfriend,
    addFriend,
} from '../containers/Friends/methods'
const { Meta } = Card

const FriendCard = props => {
    const friendActions = friendActionButton({
        accountId: props.account ? props.account._id : '',
        realFriends: props.realFriends,
        invitedFriends: props.invitedFriends,
        invitingFriends: props.invitingFriends,
        _id: props.friend._id,
        asIcons: true,
        acceptFriend: acceptFriend,
        unfriend: unfriend,
        addFriend: addFriend,
        additionalButton: (
            <Tooltip title="See profile">
                <span
                    type="link"
                    key="100"
                    onClick={() =>
                        props.history.push('/profile/' + props.friend._id)
                    }
                    style={{ fontSize: '18px' }}
                >
                    <EyeOutlined />
                </span>
            </Tooltip>
        ),
    })

    return (
        <Card
            type="inner"
            style={{ width: 200, display: 'inline-block', margin: '4px 8px' }}
            cover={
                <span
                    style={{ padding: '25px', cursor: 'pointer' }}
                    onClick={() =>
                        props.history.push('/profile/' + props.friend._id)
                    }
                >
                    <Avatar
                        src={getAvatar(props.friend._id, props.friend.image)}
                        size={150}
                    />
                </span>
            }
            actions={friendActions}
        >
            <Meta
                title={
                    <span
                        style={{ cursor: 'pointer' }}
                        onClick={() =>
                            props.history.push('/profile/' + props.friend._id)
                        }
                    >
                        {props.friend.name}
                    </span>
                }
                description={
                    <span
                        style={{ cursor: 'pointer' }}
                        onClick={() =>
                            props.history.push('/profile/' + props.friend._id)
                        }
                    >
                        {props.friend._id}
                    </span>
                }
            />
        </Card>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(FriendCard)
)
