import React, { useEffect } from 'react'
import { Route, Switch, withRouter, Redirect } from 'react-router-dom'

import Auth from './containers/Auth/Auth'
import AuthTest from './containers/Auth/AuthTest'
import './components/UI/loadStyles'
import AccountLayout from './components/Layout/Layout'
import Goals from './containers/Goals/Goals'
import Goal from './containers/Goal/Goal'
import Progresses from './containers/Progresses/Progresses'
import Progress from './containers/Progress/Progress'
import Shop from './containers/Shop/Shop'
// import Dashboard from './containers/Dashboard/Dashboard'

import Friends from './containers/Friends/Friends'
import Account from './containers/Account/Account'
import Profile from './containers/Profile/Profile'

import ActivateAccount from './containers/ActivateAccount/ActivateAccount'
import Cookies from 'universal-cookie'
import Perk from './containers/Shop/Perk'

import WishlistItem from './containers/Wishlist/WishlistItem'
import Wishlist from './containers/Wishlist/Wishlist'
import { connect } from 'react-redux'
import * as actions from './store/actions'

const cookies = new Cookies()

const App = props => {
    const { isAuthed } = props
    useEffect(() => {
        props.tryAuth()
    })

    const redirectTo = cookies.get('redirectto')

    if (redirectTo) {
        cookies.remove('redirectto', {
            path: '/',
        })
    }

    const Logout = () => {
        props.logout()
        return <Redirect to="/login" />
    }

    const routes = (
        <Switch>
            <Route path="/goals" exact component={Goals} />
            <Route path="/goals" component={Goal} />

            <Route path="/progresses" exact component={Progresses} />
            <Route path="/progresses" component={Progress} />

            <Route path="/profile" component={Profile} />

            <Route path="/friends" exact component={Friends} />

            <Route path="/account" exact component={Account} />
            {/* <Route
                    path="/dashboard"
                    exact
                    component={withProps(Dashboard, generalProps)}
                /> */}

            <Route path="/shop" exact component={Shop} />
            <Route path="/shop" component={Perk} />
            <Route path="/wishlist" exact component={Wishlist} />
            <Route path="/wishlist" component={WishlistItem} />
            {!isAuthed && <Route path="/login" exact component={Auth} />}
            <Route path="/logout" exact component={Logout} />

            <Route path="/activate-account" exact component={ActivateAccount} />

            {process.env.NODE_ENV === 'development' && !isAuthed && (
                <Route path="/logintest" exact component={AuthTest} />
            )}
            {isAuthed ? (
                <Redirect to="/progresses" />
            ) : (
                <Redirect to="/login" />
            )}
        </Switch>
    )

    return redirectTo ? (
        <Redirect to={redirectTo} />
    ) : (
        <>
            <div style={{ height: '100%' }}>
                <AccountLayout>{routes}</AccountLayout>
            </div>
            {/* {this.props.loading && <Spinner cover />} */}
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        tryAuth: () => dispatch(actions.tryAuth()),
        logout: () => dispatch(actions.logout()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
