import React, { useState } from 'react'
import axios from 'axios'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'

import Upload from 'antd/es/upload'
import Divider from 'antd/es/divider'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import DeleteOutlined from '@ant-design/icons/DeleteOutlined'
import EditOutlined from '@ant-design/icons/EditOutlined'

import Avatar from 'antd/es/avatar'
import { uploadFile } from '../../utils/uploadFile'

const Rewards = props => {
    const [previewImage, setPreviewImage] = useState('')
    const [editedReward, setEditedReward] = useState()
    const [editedRewardIndex, setEditedRewardIndex] = useState()

    const rewardsGI = props.rewards.findIndex(
        item => item.key === props.rewardKey
    )

    if (rewardsGI === -1)
        props.setRewards([
            ...props.rewards,
            {
                key: props.rewardKey,
                rewards: [],
            },
        ])

    return rewardsGI >= 0 ? (
        <div>
            <Form.Item label={props.label} key={props.rewardKey}>
                <ol>
                    {props.rewards[rewardsGI].rewards.map((item, index) => (
                        <li>
                            {item.mode === 'simple'
                                ? item.simple
                                : item.mode === 'money'
                                ? item.money
                                : item.mode === 'item'
                                ? item.itemName
                                : ''}
                            <Button
                                type="link"
                                onClick={() => {
                                    setEditedRewardIndex(index)
                                    setEditedReward(item)
                                }}
                            >
                                <EditOutlined />
                            </Button>
                            <Button
                                type="link"
                                onClick={() => {
                                    const newRewards = [...props.rewards]
                                    newRewards[rewardsGI] = {
                                        ...newRewards[rewardsGI],
                                        rewards: newRewards[
                                            rewardsGI
                                        ].rewards.filter(
                                            (item, indexIn) => indexIn !== index
                                        ),
                                    }
                                    props.setRewards(newRewards)
                                }}
                                danger={true}
                            >
                                <DeleteOutlined />
                            </Button>
                        </li>
                    ))}
                </ol>

                <Button
                    type="link"
                    onClick={() => {
                        setEditedRewardIndex('new')
                        setEditedReward({ mode: 'simple' })
                    }}
                >
                    <PlusOutlined />
                    Add rewards
                </Button>
            </Form.Item>
            <Modal
                visible={editedReward}
                title={
                    editedReward === 'new' ? 'Add new reward' : 'Edit reward'
                }
                closable={true}
                onCancel={() => setEditedReward()}
                onOk={() => {
                    const newRewards = [...props.rewards]

                    let rewardToSave = {}
                    if (editedReward.mode === 'money') {
                        rewardToSave.money = editedReward.money
                    } else {
                        rewardToSave = { ...editedReward, money: undefined }
                    }
                    if (editedRewardIndex === 'new')
                        newRewards[rewardsGI] = {
                            ...newRewards[rewardsGI],
                            rewards: [
                                ...newRewards[rewardsGI].rewards,
                                rewardToSave,
                            ],
                        }
                    else
                        newRewards[rewardsGI] = {
                            ...newRewards[rewardsGI],
                            rewards: newRewards[rewardsGI].rewards.map(
                                (item, index) => {
                                    if (index === editedRewardIndex)
                                        return rewardToSave
                                    else return item
                                }
                            ),
                        }
                    props.setRewards(newRewards)
                    setEditedReward()
                    setEditedRewardIndex()
                }}
                destroyOnClose={true}
                okText="Save"
            >
                {editedReward && (
                    <>
                        <Form.Item label="Reward type">
                            <Select
                                placeholder="Type"
                                value={editedReward.mode}
                                onSelect={value =>
                                    setEditedReward({
                                        ...editedReward,
                                        mode: value,
                                    })
                                }
                                style={{ width: '100%' }}
                            >
                                <Select.Option key="simple" value="simple">
                                    Simple reward
                                </Select.Option>
                                <Select.Option key="item" value="item">
                                    Item reward
                                </Select.Option>
                                <Select.Option key="money" value="money">
                                    Money reward
                                </Select.Option>
                            </Select>
                        </Form.Item>
                        <Divider type="horizontal" />
                        {editedReward.mode === 'simple' ? (
                            <Form.Item label="Description">
                                <Input.TextArea
                                    value={editedReward.simple}
                                    onChange={({ target: { value } }) =>
                                        setEditedReward({
                                            ...editedReward,
                                            simple: value,
                                        })
                                    }
                                    placeholder="Explain how you will reward"
                                />
                            </Form.Item>
                        ) : editedReward.mode === 'money' ? (
                            <Form.Item label="Amount">
                                <InputNumber
                                    value={editedReward.money}
                                    onChange={value =>
                                        setEditedReward({
                                            ...editedReward,
                                            money: value,
                                        })
                                    }
                                />
                            </Form.Item>
                        ) : (
                            <>
                                <Form.Item label="Images">
                                    <Upload
                                        listType="picture-card"
                                        fileList={(
                                            editedReward.itemImages || []
                                        ).map((image, index) => ({
                                            ...image,
                                            uid: index,
                                        }))}
                                        onPreview={file => {
                                            setPreviewImage(file.url)
                                        }}
                                        onChange={({ fileList }) =>
                                            setEditedReward({
                                                ...editedReward,
                                                itemImages: fileList,
                                            })
                                        }
                                        customRequest={({ file }) => {
                                            uploadFile(
                                                file,
                                                800,
                                                undefined,
                                                url => {
                                                    setEditedReward({
                                                        ...editedReward,
                                                        itemImages: [
                                                            ...(editedReward.itemImages ||
                                                                []),
                                                            { url },
                                                        ].map(
                                                            (image, index) => ({
                                                                ...image,
                                                                uid: index,
                                                            })
                                                        ),
                                                    })
                                                }
                                            )
                                        }}
                                    >
                                        {(editedReward.itemImages || [])
                                            .length >= 8 ? null : (
                                            <div>
                                                <PlusOutlined />
                                                <div className="ant-upload-text">
                                                    Upload
                                                </div>
                                            </div>
                                        )}
                                    </Upload>
                                    <Modal
                                        visible={previewImage}
                                        footer={null}
                                        onCancel={() => setPreviewImage('')}
                                    >
                                        <img
                                            alt="example"
                                            style={{ width: '100%' }}
                                            src={previewImage}
                                        />
                                    </Modal>
                                </Form.Item>
                                <Form.Item label="Name">
                                    <Input
                                        value={editedReward.itemName}
                                        onChange={({ target: { value } }) =>
                                            setEditedReward({
                                                ...editedReward,
                                                itemName: value,
                                            })
                                        }
                                    />
                                </Form.Item>
                                <Form.Item label="Description">
                                    <Input.TextArea
                                        value={editedReward.itemDescription}
                                        onChange={({ target: { value } }) =>
                                            setEditedReward({
                                                ...editedReward,
                                                itemDescription: value,
                                            })
                                        }
                                    />
                                </Form.Item>
                            </>
                        )}
                    </>
                )}
            </Modal>
        </div>
    ) : null
}

export default Rewards
