import React from 'react'

import Button from 'antd/es/button'
import Tooltip from 'antd/es/tooltip'
import FrownOutlined from '@ant-design/icons/FrownOutlined'
import SmileOutlined from '@ant-design/icons/SmileOutlined'
import DeleteOutlined from '@ant-design/icons/DeleteOutlined'

const FriendActionButton = props => {
    const {
        invitingFriends,
        invitedFriends,
        realFriends,
        _id,
        accountId,
        acceptFriend,
        addFriend,
        unfriend,
    } = props

    if (accountId === _id) return [props.additionalButton]

    const { asIcons } = props
    const ResultButtun = props =>
        asIcons ? (
            <Tooltip title={props.text}>
                <span
                    type="link"
                    key={props.key}
                    onClick={props.onClick}
                    style={{ fontSize: '18px' }}
                >
                    {props.icon}
                </span>
            </Tooltip>
        ) : (
            <Button {...props}>{props.text}</Button>
        )

    return [
        props.additionalButton,
        ...(invitingFriends.find(item => item.friend === _id)
            ? [
                  <ResultButtun
                      text="Accept"
                      key="0"
                      onClick={() => acceptFriend(_id)}
                      icon={<SmileOutlined />}
                      type="primary"
                  />,
                  <ResultButtun
                      text="Decline"
                      key="1"
                      onClick={() => unfriend(_id)}
                      icon={<DeleteOutlined />}
                      danger={true}
                  />,
              ]
            : invitedFriends.find(item => item.friend === _id)
            ? [
                  <ResultButtun
                      text="Cancel invitation"
                      key="2"
                      onClick={() => unfriend(_id)}
                      icon={<DeleteOutlined />}
                      danger={true}
                  />,
              ]
            : realFriends.find(item => item.friend === _id)
            ? [
                  <ResultButtun
                      key="3"
                      onClick={() => unfriend(_id)}
                      icon={<FrownOutlined />}
                      text="Unfriend"
                      danger={true}
                  />,
              ]
            : [
                  <ResultButtun
                      text="Become friends"
                      key="4"
                      onClick={() => addFriend(_id)}
                      icon={<SmileOutlined />}
                      type="primary"
                  />,
              ]),
    ]
}

export default FriendActionButton
