import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Button from 'antd/es/button'
import Descriptions from 'antd/es/descriptions'
import Modal from 'antd/es/modal'

const { Item } = Descriptions

const RewardsView = props => {
    const [previewImage, setPreviewImage] = useState('')
    const [previewReward, setPreviewReward] = useState()

    const rewardsGroup = props.goal.rewardsGroups.find(
        item => item.key === props.groupKey
    )
    return rewardsGroup ? (
        <>
            <ol style={{ padding: 0 }}>
                {rewardsGroup.rewards.map((item, index) => (
                    <li key={index}>
                        <Button
                            type="link"
                            onClick={() => setPreviewReward(item)}
                        >
                            {item.mode === 'simple'
                                ? item.simple
                                : item.mode === 'money'
                                ? item.money
                                : item.mode === 'item'
                                ? item.itemName
                                : ''}
                        </Button>
                    </li>
                ))}
            </ol>
            <Modal
                visible={previewReward}
                footer={null}
                onCancel={() => setPreviewReward()}
                title="Reward details"
            >
                {previewReward ? (
                    <>
                        {previewReward.itemImages &&
                            previewReward.itemImages.map((image, index) => (
                                <span
                                    key={index}
                                    onClick={() => setPreviewImage(image)}
                                >
                                    <Card
                                        type="inner"
                                        style={{
                                            width: 200,
                                            display: 'inline-block',
                                        }}
                                        hoverable
                                        cover={
                                            <div
                                                style={{
                                                    backgroundImage:
                                                        'url(' + image + ')',
                                                    height: 200,
                                                    width: 200,
                                                    backgroundSize: 'cover',
                                                }}
                                            />
                                        }
                                    />
                                </span>
                            ))}

                        <Descriptions
                            title="Reward description"
                            bordered
                            column={{
                                xxl: 4,
                                xl: 3,
                                lg: 3,
                                md: 3,
                                sm: 2,
                                xs: 1,
                            }}
                        >
                            {previewReward.mode === 'item' && (
                                <Item label="Name">
                                    {previewReward.itemName}
                                </Item>
                            )}
                            {previewReward.mode === 'item' && (
                                <Item label="Description">
                                    {previewReward.itemDescription}
                                </Item>
                            )}
                            {previewReward.mode === 'simple' && (
                                <Item label="Description">
                                    {previewReward.simple}
                                </Item>
                            )}
                            {previewReward.mode === 'money' && (
                                <Item label="Money">{previewReward.money}</Item>
                            )}
                        </Descriptions>
                    </>
                ) : null}
            </Modal>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default withRouter(RewardsView)
