import React from 'react'
import { withRouter } from 'react-router-dom'

import Card from 'antd/es/card'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal'

import FriendCard from '../../components/FriendCard'

const pendingFriends = props => {
    const {
        pendingFriendsOpen,
        setPendingFriendsOpen,
        realFriends,
        invitedFriends,
        invitingFriends,
    } = props
    return (
        <Modal
            title="Pending friends"
            visible={pendingFriendsOpen}
            maskClosable
            closable
            onCancel={() => setPendingFriendsOpen(false)}
            footer={
                <Button key="back" onClick={() => setPendingFriendsOpen(false)}>
                    Return
                </Button>
            }
        >
            {invitingFriends.length > 0 && (
                <Card title="People invited me to be friends">
                    {invitingFriends.map(friend => (
                        <FriendCard
                            key={friend._id}
                            {...props}
                            friend={friend}
                            realFriends={realFriends}
                            invitingFriends={invitingFriends}
                            invitedFriends={invitedFriends}
                        />
                    ))}
                </Card>
            )}

            {invitedFriends.length > 0 && (
                <Card title="I invited these people">
                    {invitedFriends.map(friend => (
                        <FriendCard
                            key={friend._id}
                            {...props}
                            friend={friend}
                            realFriends={realFriends}
                            invitingFriends={invitingFriends}
                            invitedFriends={invitedFriends}
                        />
                    ))}
                </Card>
            )}
        </Modal>
    )
}

export default withRouter(pendingFriends)
