import React from 'react'
import Card from 'antd/es/card'
import Tooltip from 'antd/es/tooltip'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import Meta from 'antd/es/card/Meta'

const AddCard = props => (
    <span onClick={props.action}>
        <Card
            type="inner"
            style={{
                width: 200,
                display: 'inline-block',
                margin: '4px 8px',
            }}
            hoverable
            cover={
                <div
                    style={{
                        height: 200,
                        width: 200,
                        backgroundSize: 'cover',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                    onClick={props.action}
                >
                    <PlusCircleOutlined
                        style={{
                            fontSize: '60px',
                        }}
                    />
                </div>
            }
            actions={[
                <Tooltip title="Add goal">
                    <span key="3" onClick={props.action}>
                        <PlusCircleOutlined style={{ fontSize: '18px' }} />
                    </span>
                </Tooltip>,
            ]}
        >
            <Meta
                title={
                    <span onClick={props.action} style={{ cursor: 'pointer' }}>
                        {props.title}
                    </span>
                }
                description={typeof props.sub !== 'undefined' && props.sub}
            />
        </Card>
    </span>
)

export default AddCard
