import React from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'

import EditFilled from '@ant-design/icons/EditFilled'
import Tooltip from 'antd/es/tooltip'
import DollarCircleOutlined from '@ant-design/icons/DollarCircleOutlined'

const { Meta } = Card

const PerkCard = props => {
    const {
        perk,
        perkId,
        owner,
        account,
        setEditedPerk,
        setEditPerkOpen,
        mode,
    } = props

    return (
        <Card
            type="inner"
            style={{
                width: 200,
                display: 'inline-block',
                margin: '4px 8px',
            }}
            hoverable
            cover={
                <div
                    onClick={() =>
                        props.history.push(
                            `/${mode || 'shop'}/${owner._id}/${perkId}`
                        )
                    }
                    style={{
                        backgroundImage:
                            'url(' +
                            (perk.images.length > 0 && perk.images[0]) +
                            ')',
                        height: 200,
                        width: 200,
                        backgroundSize: 'cover',
                    }}
                />
            }
            actions={[
                ...(owner._id === account._id && !props.onlyBuy
                    ? [
                          <Tooltip title="Edit">
                              <span
                                  key="1"
                                  onClick={() => {
                                      setEditedPerk(perkId)
                                      setEditPerkOpen(true)
                                  }}
                              >
                                  <EditFilled style={{ fontSize: '18px' }} />
                              </span>
                          </Tooltip>,
                      ]
                    : []),

                ...(props.mode === 'shop'
                    ? [
                          <Tooltip title="Buy">
                              <span
                                  key="1"
                                  onClick={() =>
                                      props.buyPerk(account, perk, owner, props)
                                  }
                              >
                                  <DollarCircleOutlined
                                      style={{ fontSize: '18px' }}
                                  />
                              </span>
                          </Tooltip>,
                      ]
                    : []),
            ]}
        >
            <Meta title={perk.name} description={perk.price} />
        </Card>
    )
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        buyPerk: (account, perk, owner, props) =>
            dispatch(actions.buyPerk(account, perk, owner, props)),
    }
}

export default withRouter(
    connect(
        null,
        mapDispatchToProps
    )(PerkCard)
)
