import React, { useState, useEffect } from 'react'
import axios from 'axios'

import EditGoalAndProgressForm from '../../components/EditGoalAndProgressForm/EditGoalAndProgressForm'

const AddGoal = props => {
    const [goal, setGoal] = useState()
    const { goalId } = props
    useEffect(() => {
        if (props.goalId)
            axios
                .get(`/api/goals/${props.nickname}/${props.goalId}`)
                .then(res => {
                    if (res.data.success) {
                        const milestonesValues = {}
                        for (let milestone of res.data.goal.milestonesValues) {
                            milestonesValues[milestone.key] = milestone
                        }
                        const goal = { ...res.data.goal, ...milestonesValues }
                        setGoal(goal)
                    }
                })
                .catch(err => {})
    }, [])

    const onFinish = value => {
        axios
            .post('/api/goals/add', {
                value,
                id: props.goalId,
            })
            .then(res => {
                props.setData({ ...props.data, ...res.data })
                props.setEditGoalOpen(false)
            })
            .catch(err => {})
    }

    const deleteGoal = () => {
        if (goalId) {
            axios
                .post('/api/goals/delete/' + goalId)
                .then(res => {
                    props.setData({ ...props.data, ...res.data })
                })
                .catch(err => {})
        }
        props.setEditGoalOpen(false)
    }

    return !props.goalId || goal ? (
        <EditGoalAndProgressForm
            goal={goal}
            onFinish={onFinish}
            deleteGoal={deleteGoal}
            account={props.account}
            isGoal={true}
            goalId={goalId}
        />
    ) : null
}

export default AddGoal
