import React, { useState, useRef, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'
import Comment from 'antd/es/comment'
import Avatar from 'antd/es/avatar'
import Input from 'antd/es/input'
import Form from 'antd/es/form'
import Upload from 'antd/es/upload'
import PictureOutlined from '@ant-design/icons/PictureOutlined'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import SendOutlined from '@ant-design/icons/SendOutlined'
import { uploadFile } from '../../utils/uploadFile'
import * as classes from './Messages.module.css'
import * as wsActions from '../../websocketActions'
import ProgressMessage from './ProgressMessage'
import Divider from 'antd/es/divider'
import Drawer from 'antd/es/drawer'
import Activity from './Activity'

const Messages = props => {
    const [imageLoading, setImageLoading] = useState(false)
    const { data, state, setState, setData } = props
    const { progress, account } = data
    const { goal } = progress
    const messagesEndRef = useRef(null)

    useEffect(() => {
        if (messagesEndRef && messagesEndRef.current)
            messagesEndRef.current.scrollIntoView({ behavior: 'smooth' })
    }, [props.data.progress.messages.length])
    const getMessages = messages =>
        messages && messages.length
            ? [
                  ...messages.map((message, index) => (
                      <ProgressMessage
                          key={index}
                          message={message}
                          data={data}
                          state={state}
                          setState={setState}
                      >
                          {getMessages(message.replies)}
                      </ProgressMessage>
                  )),
                  <div key="-1" ref={messagesEndRef} />,
              ]
            : null

    return goal ? (
        <>
            <div
                style={{
                    textAlign: 'left',
                    marginBottom: '200px',
                }}
                className={classes.Messages}
            >
                {getMessages(progress.messages)}
                <div
                    style={{
                        position: 'fixed',
                        bottom: '0px',
                        width: '100%',
                        background: '#fff',
                        left: '0px',
                        padding: '0px 10px',
                        boxShadow:
                            '0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 -1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2)',
                    }}
                >
                    <Comment
                        avatar={
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    account._id +
                                    '/avatar'
                                }
                            />
                        }
                        content={
                            <>
                                {state.editedMessage && (
                                    <>
                                        <span>Editing message</span>
                                        <Button
                                            type="link"
                                            onClick={() => {
                                                setState({
                                                    ...state,
                                                    replyToMessage: '',
                                                    editedMessage: '',
                                                    messageValue: '',
                                                })
                                            }}
                                        >
                                            Cancel editing
                                        </Button>
                                    </>
                                )}
                                {state.replyToMessage && (
                                    <>
                                        <span>Commenting message</span>
                                        <Button
                                            type="link"
                                            onClick={() => {
                                                setState({
                                                    ...state,
                                                    replyToMessage: '',
                                                    editedMessage: '',
                                                })
                                            }}
                                        >
                                            Cancel commenting
                                        </Button>
                                    </>
                                )}
                                <Form.Item>
                                    <Input.TextArea
                                        autoSize={{ minRows: 1, maxRows: 3 }}
                                        rows={1}
                                        onChange={e =>
                                            setState({
                                                ...state,
                                                messageValue: e.target.value,
                                            })
                                        }
                                        value={state.messageValue}
                                    />
                                </Form.Item>
                                <Form.Item>
                                    <Button
                                        htmlType="submit"
                                        onClick={() =>
                                            wsActions.sendMessage(state, data)
                                        }
                                        icon={<SendOutlined />}
                                        size="large"
                                        type="link"
                                    />
                                    <Upload
                                        showUploadList={false}
                                        customRequest={({ file }) => {
                                            setImageLoading(true)
                                            uploadFile(
                                                file,
                                                800,
                                                undefined,
                                                url => {
                                                    setImageLoading(false)
                                                    wsActions.sendMessage(
                                                        state,
                                                        data,
                                                        url
                                                    )
                                                }
                                            )
                                        }}
                                    >
                                        <Button
                                            size="large"
                                            type="link"
                                            icon={
                                                imageLoading ? (
                                                    <LoadingOutlined />
                                                ) : (
                                                    <PictureOutlined />
                                                )
                                            }
                                        />
                                    </Upload>

                                    <Button
                                        type="primary"
                                        onClick={() =>
                                            setState({
                                                ...state,
                                                activityOpen: true,
                                            })
                                        }
                                    >
                                        Activity and settings
                                    </Button>
                                </Form.Item>
                            </>
                        }
                    />
                </div>
            </div>
        </>
    ) : null
}

export default withRouter(Messages)
