import axios from 'axios'
import processRes from '../../utils/processRes'
import pullData from '../../utils/pullData'

export const acceptFriend = (id, props) => {
    axios
        .post('/api/friends/accept', {
            id,
        })
        .then(res => {
            processRes(res, props)
            if (res.data.success) {
                pullData(props, {})
            }
        })
        .catch(err => {})
}

