import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Tabs from 'antd/es/tabs'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import pullData from '../../utils/pullData'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'
import axios from 'axios'

const { Meta } = Card
const { TabPane } = Tabs

const Progresses = props => {
    const [data, setData] = useState(
        props.progressesData && props.account ? { ...props } : {}
    )

    useEffect(() => {
        if (!props.progressesData || !props.account)
            pullData(props, data, setData, {})
        if (!props.keepPageTitle && props.setTitle)
            props.setTitle({ main: 'Progresses', sub: '' })
    }, [])

    const { account, progressesData } = data

    const ProgressCard = withRouter(props => {
        const { progress } = props
        const { goal } = progress
        return (
            <div
                style={{
                    display: 'inline-block',
                    margin: '4px 8px',
                }}
                onClick={() =>
                    props.history.push('/progresses/' + progress._id)
                }
            >
                <Card
                    type="inner"
                    style={{
                        width: 200,
                    }}
                    hoverable
                    cover={
                        <div
                            onClick={() =>
                                props.history.push(
                                    '/progresses/' + progress._id
                                )
                            }
                            style={{
                                backgroundImage:
                                    goal &&
                                    'url(' +
                                        (goal.images.length > 0 &&
                                            goal.images[0]) +
                                        ')',
                                height: 200,
                                width: 200,
                                backgroundSize: 'cover',
                            }}
                        />
                    }
                >
                    {goal && (
                        <>
                            <Meta title={goal.name} />
                            <Meta
                                title={'Made by ' + progress.worker}
                                description={'In goal by ' + progress.owner}
                            />
                        </>
                    )}
                </Card>
            </div>
        )
    })

    return account ? (
        <>
            <Tabs
                style={{
                    background: 'transparent',
                    boxShadow: 'none',
                    margin: '0',
                    marginTop: '-15px',
                }}
            >
                <TabPane tab="All roles" key="1">
                    {progressesData &&
                        progressesData.map((progress, index) => (
                            <ProgressCard key={index} progress={progress} />
                        ))}
                </TabPane>
                <TabPane tab="Achiever" key="2">
                    {progressesData &&
                        progressesData
                            .filter(progress => progress.worker === account._id)
                            .map((progress, index) => (
                                <ProgressCard key={index} progress={progress} />
                            ))}
                </TabPane>
                <TabPane tab="Motivator" key="3">
                    {progressesData &&
                        progressesData
                            .filter(progress => progress.owner === account._id)
                            .map((progress, index) => (
                                <ProgressCard key={index} progress={progress} />
                            ))}
                </TabPane>
                <TabPane tab="Expert" key="4">
                    {progressesData &&
                        progressesData
                            .filter(
                                progress =>
                                    progress.goal &&
                                    (progress.goal.experts.includes(
                                        account._id
                                    ) ||
                                        progress.goal.milestonesValues.find(
                                            milestone =>
                                                milestone.experts.includes(
                                                    account._id
                                                )
                                        ))
                            )
                            .map((progress, index) => (
                                <ProgressCard key={index} progress={progress} />
                            ))}
                </TabPane>
                <TabPane tab="Supporter" key="5">
                    {progressesData &&
                        progressesData
                            .filter(
                                progress =>
                                    progress.goal &&
                                    (progress.goal.supporters.includes(
                                        account._id
                                    ) ||
                                        progress.goal.milestonesValues.find(
                                            milestone =>
                                                milestone.supporters.includes(
                                                    account._id
                                                )
                                        ))
                            )
                            .map((progress, index) => (
                                <ProgressCard key={index} progress={progress} />
                            ))}
                </TabPane>
            </Tabs>
        </>
    ) : null
}

export default withRouter(Progresses)
