import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import PageHeader from 'antd/es/page-header'
import Form from 'antd/es/form'
import Button from 'antd/es/button'
import axios from 'axios'
import Input from 'antd/es/input'

import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Upload from 'antd/es/upload'
import * as classes from './Account.module.css'

import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'
import processRes from '../../utils/processRes'
import processResErr from '../../utils/processResErr'
import Modal from 'antd/es/modal'
import message from 'antd/es/message'

const Account = props => {
    const [deleteVisible, setDeleteVisible] = useState()
    const [deleteField, setDeleteField] = useState('')
    const { account } = props

    useEffect(() => {
        pullData(props, {})
        props.setPageTitle({ main: 'Account', sub: '' })
    }, [])

    const loading = false

    const deleteAccount = () => {
        if (deleteField !== account._id) {
            message.warning('The nickmanme is not correct.')
        } else {
            axios
                .post('/api/account/delete', {
                    accountId: account._id,
                })
                .then(res => {
                    props.history.push('/logout')
                })
                .catch(err => {
                    processResErr(err)
                })
        }
    }

    const onFinish = value => {
        axios
            .post('/api/account', {
                name: value.name,
            })
            .then(res => {
                processRes(res, props)
            })
            .catch(err => {
                processResErr(err)
            })
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 0 },
    }
    return account && account.name ? (
        <Card>
            <PageHeader
                className="site-page-header"
                title="My account"
                backIcon={false}
            />
            <Upload
                name="avatar"
                listType="picture-card"
                className={classes.Avatar}
                showUploadList={false}
                customRequest={({ file }) => {
                    uploadFile(file, 800, 'avatar', () => {
                        props.increaseAvatarV()
                    })
                }}
            >
                {account.image ? (
                    <Avatar
                        src={getAvatar(account._id, account.image)}
                        size={150}
                        style={{ margin: '25px' }}
                    />
                ) : (
                    <div>
                        {loading ? <LoadingOutlined /> : <PlusOutlined />}
                        <div className="ant-upload-text">Upload</div>
                    </div>
                )}
            </Upload>
            <Form
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                {...layout}
                initialValues={{
                    name: account.name,
                    nickname: account._id,
                }}
            >
                <Form.Item
                    label="Name"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: 'Name is too short',
                            min: 2,
                            whitespace: true,
                        },
                    ]}
                >
                    <Input placeholder="Must be more than 1 character long" />
                </Form.Item>
                <Form.Item label="Nickname" name="nickname">
                    <Input disabled={true} />
                </Form.Item>
                <Form.Item
                    {...tailLayout}
                    style={{
                        flex: 'none',
                        maxWidth: ' 100%',
                        overflow: 'hidden',
                    }}
                >
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>
                </Form.Item>
                <Form.Item
                    {...tailLayout}
                    style={{
                        flex: 'none',
                        maxWidth: ' 100%',
                        overflow: 'hidden',
                    }}
                >
                    <Button
                        type="dashed"
                        onClick={() => setDeleteVisible(true)}
                        danger={true}
                        size="large"
                    >
                        Delete account
                    </Button>
                </Form.Item>
            </Form>
            <Modal
                title="Delete account"
                visible={deleteVisible}
                onOk={deleteAccount}
                onCancel={() => {
                    setDeleteVisible(false)
                    setDeleteField('')
                }}
            >
                <p>
                    Please enter your nickname and press Ok to delete your
                    account.
                </p>
                <Input onChange={e => setDeleteField(e.target.value)} />
            </Modal>
        </Card>
    ) : null
}

const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        setAvatarV: v => dispatch(actions.setAvatarV(v)),
        increaseAvatarV: () => dispatch(actions.increaseAvatarV()),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Account)
)
