import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import { uploadFile } from '../../utils/uploadFile'
import AddPerkForm from './AddPerkForm'
import { withRouter } from 'react-router-dom'
import processRes from '../../utils/processRes'

const AddPerk = props => {
    const [perk, setPerk] = useState()
    const [images, setImages] = useState([])
    const { account, mode } = props
    const perkId = props.perkId || ''
    useEffect(() => {
        if (perkId)
            axios
                .get(`/api/${mode || 'shop'}/${account._id}/${perkId}`)
                .then(res => {
                    if (res.data.success) {
                        console.log(res.data)
                        const perk =
                            mode === 'wishlist'
                                ? res.data.wishlistItem
                                : res.data.perk
                        setPerk(perk)
                        setImages(
                            perk.images.map((url, index) => ({
                                url,
                                uid: index,
                            }))
                        )
                    }
                })
                .catch(err => {})
    }, [])

    const onFinish = value => {
        axios
            .post(`/api/${mode || 'shop'}/add`, {
                value: { ...value, images: images.map(image => image.url) },
                id: perkId,
            })
            .then(res => {
                processRes(res, props)
                props.setEditPerkOpen(false)
                props.setData(res.data)
            })
            .catch(err => {})
    }

    const deletePerk = () => {
        if (perkId) {
            axios
                .post(`/api/${mode || 'shop'}/delete/${perkId}`)
                .then(res => {
                    processRes(res, props)
                    props.setData(res.data)
                })
                .catch(err => {})
        }
        props.setEditPerkOpen(false)
    }

    const connections = account.friendsData || []

    const uploadImage = file => {
        uploadFile(file, 800, undefined, url => {
            setImages(
                [...images, { url }].map((image, index) => ({
                    ...image,
                    uid: index,
                }))
            )
        })
    }

    return !props.perkId || perk ? (
        <AddPerkForm
            connections={connections}
            perkId={perkId}
            perk={perk}
            deletePerk={deletePerk}
            onFinish={onFinish}
            images={images}
            setImages={setImages}
            uploadImage={uploadImage}
            mode={props.mode}
        />
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(AddPerk)
)
