import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'
import Switch from 'antd/es/switch'
import Tabs from 'antd/es/tabs'
import Divider from 'antd/es/divider'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import Avatar from 'antd/es/avatar'
import DraggableTabs from '../../components/UI/DraggableTabs/DraggableTabs'
import { uploadFile } from '../../utils/uploadFile'

import * as wsActions from '../../websocketActions'
import EditGoalAndProgressForm from '../../components/EditGoalAndProgressForm/EditGoalAndProgressForm'

const EditProgress = props => {
    const { data, state, setState } = props
    const { progress } = data

    const milestonesValues = {}
    for (let milestone of progress.goal.milestonesValues) {
        milestonesValues[milestone.key] = milestone
    }
    const goal = { ...progress.goal, ...milestonesValues }

    const onFinish = value => {
        wsActions.editGoalInProgress(data, value)
        setState({ ...state, editProgressOpen: false })
    }

    return goal ? (
        <EditGoalAndProgressForm
            goal={goal}
            onFinish={onFinish}
            account={data.account}
        />
    ) : null
}

export default EditProgress
