import React from 'react'
import { withRouter } from 'react-router-dom'

import Comment from 'antd/es/comment'

import Tooltip from 'antd/es/tooltip'
import Avatar from 'antd/es/avatar'
import TimeAgo from 'timeago-react'

import * as wsActions from '../../websocketActions'
import LikeFilled from '@ant-design/icons/LikeFilled'
import LikeOutlined from '@ant-design/icons/LikeOutlined'
import DislikeFilled from '@ant-design/icons/DislikeFilled'
import DislikeOutlined from '@ant-design/icons/DislikeOutlined'
import Card from 'antd/es/card'

const ProgressMessage = props => {
    const { data, state, setState, message } = props
    const { account } = data

    const changeLikes = (type, messageId) => {
        wsActions.changeLikesMessage(data, type, messageId)
    }
    return (
        <Card>
            <Comment
                actions={[
                    <span key="comment-basic-like">
                        <Tooltip title="Like">
                            {message.likes.includes(account._id) ? (
                                <LikeFilled
                                    onClick={() =>
                                        changeLikes(
                                            'removeLikeMessage',
                                            message.messageId
                                        )
                                    }
                                />
                            ) : (
                                <LikeOutlined
                                    onClick={() =>
                                        changeLikes(
                                            'likeMessage',
                                            message.messageId
                                        )
                                    }
                                />
                            )}
                        </Tooltip>
                        <span style={{ paddingLeft: '8px' }}>
                            {message.likes.length}
                        </span>
                    </span>,
                    <span key="comment-basic-dislike">
                        <Tooltip title="Dislike">
                            {message.dislikes.includes(account._id) ? (
                                <DislikeFilled
                                    onClick={() =>
                                        changeLikes(
                                            'removeDislikeMessage',
                                            message.messageId
                                        )
                                    }
                                />
                            ) : (
                                <DislikeOutlined
                                    onClick={() =>
                                        changeLikes(
                                            'dislikeMessage',
                                            message.messageId
                                        )
                                    }
                                />
                            )}
                        </Tooltip>
                        <span style={{ paddingLeft: '8px' }}>
                            {message.dislikes.length}
                        </span>
                    </span>,
                    <span
                        key="comment-basic-reply-to"
                        onClick={() => {
                            setState({
                                ...state,
                                replyToMessage: message.messageId,
                                editedMessage: '',
                            })
                        }}
                    >
                        Reply to
                    </span>,
                    <span
                        key="comment-basic-reply-to"
                        onClick={() => {
                            setState({
                                ...state,
                                messageValue: message.text,
                                editedMessage: message.messageId,
                                replyToMessage: '',
                            })
                        }}
                    >
                        Edit
                    </span>,
                ]}
                author={
                    <a>
                        {account.friendsData.find(
                            item => item._id === message.author
                        ).name +
                            ' (' +
                            message.author +
                            ')'}
                    </a>
                }
                avatar={
                    <Avatar
                        src={
                            'https://websiter.s3.us-east-2.amazonaws.com/' +
                            message.author +
                            '/avatar'
                        }
                    />
                }
                content={
                    message.action === 'message' ? (
                        <p>{message.text}</p>
                    ) : message.action === 'image' ? (
                        <div
                            style={{
                                backgroundImage: 'url(' + message.text + ')',
                                width: '200px',
                                height: '200px',
                                backgroundSize: 'cover',
                                cursor: 'pointer',
                            }}
                            onClick={() =>
                                setState({
                                    ...state,
                                    previewImage: message.text,
                                })
                            }
                        />
                    ) : null
                }
                datetime={
                    <span>
                        <TimeAgo datetime={message.date} live />
                    </span>
                }
            >
                {props.children}
            </Comment>
        </Card>
    )
}

export default withRouter(ProgressMessage)
