import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

import Button from 'antd/es/button'

import axios from 'axios'
import pullData from '../../utils/pullData'
import FriendCard from '../../components/FriendCard'
import AddCard from '../../components/AddCard/AddCard'
import Divider from 'antd/es/divider'
import message from 'antd/es/message'
import AddFriends from './AddFriends'
import PendingFriends from './PendingFriends'

const Friends = props => {
    const [addFriendOpen, setAddFriendOpen] = useState()
    const [pendingFriendsOpen, setPendingFriendsOpen] = useState()
    const [searchResult, setSearchResult] = useState(null)

    useEffect(() => {
        pullData(props, {})
        props.setPageTitle({ main: 'Friends', sub: '' })
    }, [])

    const { account } = props
    if (!account) return null
    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []

    if (account.friendsData && account.friends) {
        for (let friend of account.friendsData) {
            const friendRef = account.friends.find(
                item => item.friend.toString() === friend._id.toString()
            )
            const fullFriend = { ...friendRef, ...friend }
            if (friendRef) {
                if (friendRef.status === 'friend') realFriends.push(fullFriend)
                else if (friendRef.status === 'invited')
                    invitedFriends.push(fullFriend)
                else if (friendRef.status === 'inviting')
                    invitingFriends.push(fullFriend)
            }
        }
    }

    const onFinish = value => {
        axios
            .post('/api/friends/find', {
                ...value,
            })
            .then(res => {
                if (!res.data.success) {
                    message.warning(
                        'Accounts with nickname ' +
                            value.search +
                            ' do not exist.'
                    )
                    setSearchResult(null)
                } else setSearchResult(res.data.friend)
            })
            .catch(err => {})
    }

    return (
        <>
            {(invitedFriends.length > 0 || invitingFriends.length > 0) && (
                <>
                    <Button
                        style={{ margin: '30px 0px ' }}
                        type="primary"
                        onClick={() => setPendingFriendsOpen(true)}
                    >
                        Show pending friends
                    </Button>
                    <Divider type="horizontal" />
                </>
            )}
            <AddCard
                title="Add friend"
                sub="Find by nickname"
                action={() => setAddFriendOpen(true)}
            />

            {realFriends.map(friend => (
                <FriendCard
                    key={friend._id}
                    {...props}
                    friend={friend}
                    realFriends={realFriends}
                    invitingFriends={invitingFriends}
                    invitedFriends={invitedFriends}
                />
            ))}
            <AddFriends
                addFriendOpen={addFriendOpen}
                setAddFriendOpen={setAddFriendOpen}
                onFinish={onFinish}
                searchResult={searchResult}
                realFriends={realFriends}
                invitedFriends={invitedFriends}
                invitingFriends={invitingFriends}
                account={account}
            />
            <PendingFriends
                pendingFriendsOpen={pendingFriendsOpen}
                setPendingFriendsOpen={setPendingFriendsOpen}
                realFriends={realFriends}
                invitedFriends={invitedFriends}
                invitingFriends={invitingFriends}
            />
        </>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Friends)
)
