import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Button from 'antd/es/button'
import Table from 'antd/es/table'
import Tag from 'antd/es/tag'
import RewardsView from '../../components/RewardsView/RewardsView'
import TimeAgo from 'timeago-react'
import Card from 'antd/es/card'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'

const Transactions = props => {
    props.setPageTitle({ main: 'Shop', sub: 'Transactions' })

    const { account } = props
    if (!account) return null
    const { transactionsData, friendsData } = account

    const columns = [
        {
            title: 'Type',
            dataIndex: 'types',
            key: 'types',
            render: types => (
                <>
                    {types.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green'
                        if (tag === 'loser') {
                            color = 'volcano'
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        )
                    })}
                </>
            ),
        },
        {
            title: 'With',
            dataIndex: 'user',
            key: 'user',
            render: user => {
                const friend = friendsData.find(item => item._id === user)
                return friend ? (
                    <Button
                        type="link"
                        style={{ padding: 0 }}
                        onClick={() => props.history.push('/profile/' + user)}
                    >
                        <Avatar
                            src={getAvatar(user, friend.image)}
                            style={{ marginRight: '5px' }}
                        />
                        {friend.name + ' (' + friend._id + ')'}
                    </Button>
                ) : (
                    user
                )
            },
        },
        {
            title: 'Item',
            dataIndex: 'perk',
            key: 'perk',
            render: perk =>
                perk.simple || (
                    <RewardsView
                        goal={{
                            rewardsGroups: [{ rewards: [perk], key: 1 }],
                        }}
                        groupKey={1}
                    />
                ),
        },
        {
            title: 'Amount',
            dataIndex: 'amount',
            key: 'amount',
            render: amount => (amount > 0 ? amount : ''),
        },
        {
            title: 'Date',
            dataIndex: 'date',
            key: 'date',
            render: date => <TimeAgo datetime={date} live />,
        },
        {
            title: 'Progress',
            dataIndex: 'progress',
            key: 'progress',
            render: progress => (
                <Button
                    type="link"
                    onClick={() =>
                        props.history.push('/progresses/' + progress)
                    }
                >
                    {progress}
                </Button>
            ),
        },
        {
            title: 'Status',
            key: 'status',
            dataIndex: 'status',
            render: (status, record) => {
                if (status === 'Not confirmed') {
                    return (
                        <>
                            <Button
                                danger={true}
                                type="link"
                                onClick={() =>
                                    props.cancelTransaction(record.transaction._id, props)
                                }
                            >
                                Cancel
                            </Button>
                            {record.transaction.from === account._id && (
                                <Button
                                    type="link"
                                    onClick={() =>
                                        props.confirmTransaction(
                                            record.transaction._id, props
                                        )
                                    }
                                >
                                    Confirm
                                </Button>
                            )}
                        </>
                    )
                }
                return status
            },
        },
    ]

    const tableData = []
    let sorted = [...transactionsData]
    sorted = sorted.sort((a, b) =>
        new Date(a.date).getTime() < new Date(b.date).getTime() ? 1 : -1
    )
    for (let transaction of sorted) {
        const types = []
        if (transaction.from === account._id && transaction.to === account.id) {
            types.push('internal')
        }
        if (transaction.to === account._id) {
            if (transaction.progress) {
                types.push('earn')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('buy')
                    types.push('item')
                } else {
                    types.push('recieve gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        } else if (transaction.from === account._id) {
            if (transaction.progress) {
                types.push('pay')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('sell')
                    types.push('item')
                } else {
                    types.push('send gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        }

        const user =
            transaction.to !== account._id ? transaction.to : transaction.from

        tableData.push({
            key: tableData.length,
            types,
            user,
            perk: transaction.item,
            amount: transaction.amount,
            date: transaction.date,
            progress: transaction.progress,
            status: transaction.status,
            transaction,
        })
    }
    return (
        <Card>
            <Table columns={columns} dataSource={tableData} />
        </Card>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        cancelTransaction: (id, props) =>
            dispatch(actions.cancelTransaction(id, props)),
        confirmTransaction: (id, props) =>
            dispatch(actions.confirmTransaction(id, props)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Transactions)
)
