import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Tabs from 'antd/es/tabs'
import pullData from '../../utils/pullData'
import Wallet from './Wallet'
import Transactions from './Transactions'
import WishlistAndPerks from './WishlistAndPerks'

const Shop = props => {
    useEffect(() => {
        pullData(props, {})
        props.setPageTitle({ main: 'Shop', sub: 'My items' })
    }, [])

    return props.account ? (
        <Tabs
            style={{
                background: 'transparent',
                boxShadow: 'none',
                margin: '0',
                marginTop: '-15px',
            }}
        >
            <Tabs.TabPane tab="My shop" key={1}>
                <WishlistAndPerks mode="shop" listKey="perks" />
            </Tabs.TabPane>
            <Tabs.TabPane tab="Wallet" key={3}>
                <Wallet />
            </Tabs.TabPane>
            <Tabs.TabPane tab="Transactions" key={4}>
                <Transactions />
            </Tabs.TabPane>
        </Tabs>
    ) : (
        <span />
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: () => dispatch(actions.tryAuth()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Shop)
)
