import React from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import EditFilled from '@ant-design/icons/EditFilled'
import RocketOutlined from '@ant-design/icons/RocketOutlined'
import TeamOutlined from '@ant-design/icons/TeamOutlined'

import startProgress from '../../Actions/startProgress'
import Tooltip from 'antd/es/tooltip'

const GoalCard = withRouter(props => {
    const {
        goal,
        owner,
        available,
        account,
        setEditedGoal,
        setEditGoalOpen,
        setInviteGoal,
    } = props

    return (
        <Card
            type="inner"
            style={{
                width: 200,
                display: 'inline-block',
                margin: '4px 8px',
            }}
            cover={
                <div
                    onClick={() =>
                        props.history.push(
                            '/goals/' + owner._id + '/' + goal.goalId
                        )
                    }
                    style={{
                        backgroundImage:
                            'url(' +
                            (goal.images.length > 0 && goal.images[0]) +
                            ')',
                        height: 200,
                        width: 200,
                        backgroundSize: 'cover',
                        cursor: 'pointer',
                    }}
                />
            }
            actions={[
                owner._id === account._id && setEditedGoal && (
                    <Tooltip title="Edit">
                        <span
                            key="1"
                            onClick={() => {
                                setEditedGoal(goal.goalId)
                                setEditGoalOpen(true)
                            }}
                        >
                            <EditFilled style={{ fontSize: '18px' }} />
                        </span>
                    </Tooltip>
                ),
                (available || owner._id === account._id) && (
                    <Tooltip title="Start progress">
                        <span
                            key="2"
                            onClick={() =>
                                startProgress(
                                    goal.goalId,
                                    owner._id,
                                    account._id
                                )
                            }
                        >
                            <RocketOutlined style={{ fontSize: '18px' }} />
                        </span>
                    </Tooltip>
                ),
                owner._id === account._id && setInviteGoal && (
                    <Tooltip title="Invite friends">
                        <span
                            type="link"
                            key="3"
                            onClick={() => {
                                setInviteGoal(goal)
                            }}
                        >
                            <TeamOutlined style={{ fontSize: '18px' }} />
                        </span>
                    </Tooltip>
                ),
            ]}
        >
            <Card.Meta
                title={
                    <span
                        onClick={() =>
                            props.history.push(
                                '/goals/' + owner._id + '/' + goal.goalId
                            )
                        }
                        style={{ cursor: 'pointer' }}
                    >
                        {goal.name}
                    </span>
                }
            />
        </Card>
    )
})

export default withRouter(GoalCard)
