import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import PageHeader from 'antd/es/page-header'
import Descriptions from 'antd/es/descriptions'
import Tabs from 'antd/es/tabs'
import Divider from 'antd/es/divider'
import Modal from 'antd/es/modal'
import Avatar from 'antd/es/avatar'
import RewardsView from '../../components/RewardsView/RewardsView'

const { Item } = Descriptions

const Goal = props => {
    const [data, setData] = useState({})
    const [previewImage, setPreviewImage] = useState('')
    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])

    const { goal, profile } = data

    const getAccounts = items => {
        if (profile && profile.friendsData) {
            return items.map((item, index) => {
                const friendObj = profile.friendsData.find(
                    friend => friend._id === item
                )
                if (friendObj) {
                    return (
                        <span
                            key={index}
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push('/profile/' + item)
                            }
                        >
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    item +
                                    '/avatar'
                                }
                                size={20}
                                style={{ marginRight: '10px' }}
                            />
                            {friendObj.name} - ({item})
                        </span>
                    )
                } else return item
            })
        } else return items
    }
    if (goal && props.setTitle) {
        props.setTitle({ main: goal.name, sub: goal.owner })
    }
    return goal ? (
        <Card>
            {goal.images.map((image, index) => (
                <span key={index} onClick={() => setPreviewImage(image)}>
                    <Card
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        hoverable
                        cover={
                            <div
                                style={{
                                    backgroundImage: 'url(' + image + ')',
                                    height: 200,
                                    width: 200,
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                    />
                </span>
            ))}
            <p>{goal.description}</p>

            <Descriptions
                title="Goal description"
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Users">{getAccounts(goal.users)}</Item>
                <Item label="Supporters">{goal.supporters}</Item>
                <Item label="Experts">{goal.experts}</Item>
                <Item label="Start reward">
                    <RewardsView groupKey="start" goal={goal} />
                </Item>
                <Item label="Finish reward">
                    <RewardsView groupKey="finish" goal={goal} />
                </Item>
                <Item label="Total price">{goal.totalPrice}</Item>
                <Item label="With milestones">
                    {goal.withMilestones ? 'Yes' : 'No'}
                </Item>
            </Descriptions>
            {goal.withMilestones && (
                <>
                    <Divider>Milestones</Divider>

                    <Tabs tabPosition="left">
                        {goal.milestones.map(milestone => (
                            <Tabs.TabPane
                                tab={milestone.name}
                                key={milestone.key}
                            >
                                <h2>{milestone.name}</h2>
                                <p>{milestone.description}</p>
                                <Descriptions
                                    title="Milestone description"
                                    bordered
                                    column={{
                                        xxl: 4,
                                        xl: 3,
                                        lg: 3,
                                        md: 3,
                                        sm: 2,
                                        xs: 1,
                                    }}
                                >
                                    <Item label="Rewards">
                                        <RewardsView
                                            groupKey={milestone.key}
                                            goal={goal}
                                        />
                                    </Item>
                                </Descriptions>
                            </Tabs.TabPane>
                        ))}
                    </Tabs>
                    <Modal
                        visible={previewImage}
                        footer={null}
                        onCancel={() => setPreviewImage('')}
                    >
                        <img
                            alt="example"
                            style={{ width: '100%' }}
                            src={previewImage}
                        />
                    </Modal>
                </>
            )}
        </Card>
    ) : null
}

export default withRouter(Goal)
