import React from 'react'
import Button from 'antd/es/button'
import Table from 'antd/es/table'
import Checkbox from 'antd/es/checkbox'
import Modal from 'antd/es/modal'
import Descriptions from 'antd/es/descriptions'

import * as wsActions from '../../websocketActions'
import RewardsView from '../../components/RewardsView/RewardsView'
const { Item } = Descriptions

const ActivityTable = props => {
    const { data, state, setState } = props
    const { progress, account } = data
    const { goal } = progress

    const renderCheckbox = ({ checked, milestoneId }) => (
        <Checkbox
            checked={checked}
            onChange={e =>
                wsActions.changeStage(data, e.target.checked, milestoneId)
            }
        />
    )

    const columns = [
        {
            title: 'Milestone',
            dataIndex: 'name',
            key: 'name',
            render: milestone => (
                <Button
                    type="link"
                    onClick={() =>
                        setState({ ...state, previewMilestone: milestone })
                    }
                >
                    {milestone.name}
                </Button>
            ),
        },
        {
            title: 'Price',
            dataIndex: 'price',
            key: 'price',
        },
        {
            title: 'Status',
            dataIndex: 'status',
            key: 'status',
            render: status => (
                <>
                    <div>{status ? 'Paid:' : 'Not finished'}</div>
                    {status && (
                        <RewardsView
                            goal={{
                                rewardsGroups: [{ rewards: status, key: 1 }],
                            }}
                            groupKey={1}
                        />
                    )}
                </>
            ),
        },
        {
            title: 'Worker',
            dataIndex: 'worker',
            key: 'worker',
            render: renderCheckbox,
        },
        {
            title: 'Owner',
            dataIndex: 'owner',
            key: 'owner',
            render: renderCheckbox,
        },
        ...goal.experts.map(expert => ({
            title: expert,
            dataIndex: expert,
            key: expert,
            render: renderCheckbox,
        })),
    ]

    const getCheckboxStatus = (id, stage, milestone) => {
        return {
            checked: stage.approvedBy.find(item => item.accountId === id),
            milestoneId: milestone.key,
        }
    }

    const startStage = progress.stages.find(
        stage => stage.milestoneId === 'start'
    ) || { approvedBy: [] }

    const startExperts = {}
    for (let expert of goal.experts) {
        startExperts[expert] = getCheckboxStatus(expert, startStage, {
            key: 'start',
        })
    }

    const startData = {
        key: 'start',
        name: {
            name: 'Start',
            description:
                'At this stage owner and user should agree to start this progress.',
            key: 'start',
        },
        price: progress.startPrice,
        status: startStage.paid,
        worker: getCheckboxStatus(progress.worker, startStage, {
            key: 'start',
        }),
        owner: getCheckboxStatus(progress.owner, startStage, {
            key: 'start',
        }),
        ...startExperts,
    }

    const finishStage = progress.stages.find(
        stage => stage.milestoneId === 'finish'
    ) || { approvedBy: [] }

    const finishExperts = {}
    for (let expert of goal.experts) {
        finishExperts[expert] = getCheckboxStatus(expert, finishStage, {
            key: 'finish',
        })
    }

    const finishData = {
        key: 'finish',
        name: {
            name: 'Finish',
            description:
                'At this stage owner and user should agree to finish this progress.',
            key: 'finish',
        },
        price: progress.price,
        status: finishStage.paid,
        worker: getCheckboxStatus(progress.worker, finishStage, {
            key: 'finish',
        }),
        owner: getCheckboxStatus(progress.owner, finishStage, {
            key: 'finish',
        }),
        ...finishExperts,
    }

    const milestonesData = [
        startData,
        ...goal.milestonesValues.map((milestone, index) => {
            const stage = progress.stages.find(
                stage => stage.milestoneId === milestone.key
            ) || { approvedBy: [] }

            const experts = {}
            for (let expert of goal.experts) {
                experts[expert] = getCheckboxStatus(expert, stage, milestone)
            }
            return {
                key: index,
                name: milestone,
                price: milestone.price,
                status: stage.paid,
                worker: getCheckboxStatus(progress.worker, stage, milestone),
                owner: getCheckboxStatus(progress.owner, stage, milestone),
                ...experts,
            }
        }),
        finishData,
    ]

    return (
        <>
            <Table columns={columns} dataSource={milestonesData} />
            <Modal
                visible={state.previewMilestone}
                footer={null}
                onCancel={() => setState({ ...state, previewMilestone: '' })}
                closable={true}
            >
                {state.previewMilestone ? (
                    <>
                        <h2>{state.previewMilestone.name}</h2>
                        <p>{state.previewMilestone.description}</p>
                        <Descriptions
                            title="Milestone description"
                            bordered
                            column={{
                                xxl: 4,
                                xl: 3,
                                lg: 3,
                                md: 3,
                                sm: 2,
                                xs: 1,
                            }}
                        >
                            <Item label="Rewards">
                                <RewardsView
                                    groupKey={state.previewMilestone.key}
                                    goal={goal}
                                />
                            </Item>
                        </Descriptions>
                    </>
                ) : null}
            </Modal>
        </>
    )
}
export default ActivityTable
