import React from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'

const { Meta } = Card

const Wallet = props => {
    props.setPageTitle({ main: 'Shop', sub: 'Wallet' })
    return (
        props.account.wallet &&
        props.account.wallet.map((money, index) => (
            <div
                onClick={() => props.history.push('/profile/' + money.user)}
                style={{ display: 'inline-block', margin: '4px 8px' }}
            >
                <Card
                    key={index}
                    type="inner"
                    style={{
                        width: 200,
                    }}
                    cover={
                        <span style={{ padding: '25px' }}>
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    money.user +
                                    '/avatar'
                                }
                                size={150}
                            />
                        </span>
                    }
                    hoverable
                    actions={[
                        <Button key="1" icon={<PlusCircleOutlined />}>
                            Go to the shop
                        </Button>,
                    ]}
                >
                    <Meta title={money.user} description={money.amount} />
                </Card>
            </div>
        ))
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Wallet)
)
