import React from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Button from 'antd/es/button'
import PageHeader from 'antd/es/page-header'
import Divider from 'antd/es/divider'
import Modal from 'antd/es/modal'
import Tooltip from 'antd/es/tooltip'
import Avatar from 'antd/es/avatar'

import Descriptions from 'antd/es/descriptions'

const { Item } = Descriptions

import * as wsActions from '../../websocketActions'
import ActivityTable from './ActivityTable'
import EditProgress from './EditProgress'
import RewardsView from '../../components/RewardsView/RewardsView'

const Activity = props => {
    const { data, state, setState } = props
    const { progress, account } = data
    const { goal } = progress
    return goal ? (
        <>
            {goal.images.map((image, index) => (
                <span
                    key={index}
                    onClick={() =>
                        setState({
                            ...state,
                            previewImage: image,
                        })
                    }
                >
                    <Card
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        hoverable
                        cover={
                            <div
                                style={{
                                    backgroundImage: 'url(' + image + ')',
                                    height: 200,
                                    width: 200,
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                    />
                </span>
            ))}
            <h1>{goal.name}</h1>

            <p>{goal.description}</p>

            <Descriptions
                title="Goal description"
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Status">{progress.status}</Item>
                <Item label="Worker">{progress.worker}</Item>
                <Item label="Owner">{progress.owner}</Item>
                <Item label="Supporters">{goal.supporters}</Item>
                <Item label="Experts">{goal.experts}</Item>
                <Item label="Start reward">
                    <RewardsView groupKey="start" goal={goal} />
                </Item>
                <Item label="Finish reward">
                    <RewardsView groupKey="finish" goal={goal} />
                </Item>
                <Item label="With milestones">
                    {goal.withMilestones ? 'Yes' : 'No'}
                </Item>
            </Descriptions>
            <Divider orientation="center" type="horizontal">
                Milestones
            </Divider>
            <ActivityTable data={data} state={state} setState={setState} />
            <Button
                style={{ margin: '0px 20px 50px 0px' }}
                type="primary"
                onClick={() => {
                    setState({ ...state, editProgressOpen: true })
                }}
            >
                Edit progress
            </Button>
            <Button
                danger={true}
                onClick={() => {
                    wsActions.leaveProgress(data)
                }}
            >
                Leave the progress
            </Button>

            <Modal
                visible={state.editProgressOpen}
                footer={null}
                onCancel={() => setState({ ...state, editProgressOpen: false })}
                closable={true}
                destroyOnClose={true}
                width="80%"
            >
                <EditProgress data={data} state={state} setState={setState} />
            </Modal>
        </>
    ) : null
}

export default withRouter(Activity)
